import React from "react";

type Props = {
  name: string;
  price: string;
  image?: string; // optional service image
};

export default function ServiceCard({ name, price, image }: Props) {
  return (
    <div className="border rounded-lg shadow hover:shadow-lg transition p-4 flex flex-col items-center bg-white">
      <img
        src={image || "/default-service.jpg"} // default image if none
        alt={name}
        className="w-32 h-32 rounded-md mb-4 object-cover border"
      />
      <h3 className="text-lg font-bold text-center">{name}</h3>
      <p className="text-gray-700 mt-2">{price}</p>
      <button className="mt-4 bg-green-500 text-white px-4 py-1 rounded hover:bg-green-600 transition">
        Book Now
      </button>
    </div>
  );
}

