"use client";
import React, { useState, useEffect } from "react";
import ContractorCard from "./components/ContractorCard";
import ServiceCard from "./components/ServiceCard";
import Navbar from "./components/Navbar";
import { API_BASE } from "./config";

// Type definitions
type Contractor = {
  _id: string;
  name: string;
  speciality: string;
  rating: number;
  image?: string;
};

type Service = {
  _id: string;
  name: string;
  price: string;
  image?: string;
};

type User = {
  _id: string;
  name: string;
  role: string;
  apartmentInfo?: string;
  servicesProvided?: string[];
  experience?: number;
  contact?: string;
};

export default function Home() {
  const [userRole, setUserRole] = useState<string | null>(null);
  const [contractors, setContractors] = useState<Contractor[]>([]);
  const [services, setServices] = useState<Service[]>([]);
  const [members, setMembers] = useState<User[]>([]);

  useEffect(() => {
    const role = localStorage.getItem("role"); // saved on login
    setUserRole(role);

    if (role === "user") {
      fetch(`${API_BASE}/service/all`)
        .then(res => res.json())
        .then(data => setServices(data))
        .catch(err => console.error(err));

      fetch(`${API_BASE}/contractor/all`)
        .then(res => res.json())
        .then(data => setContractors(data))
        .catch(err => console.error(err));
    }

    if (role === "contractor") {
      fetch(`${API_BASE}/user/all`)
        .then(res => res.json())
        .then(data => setMembers(data.filter((u: User) => u.role === "user")))
        .catch(err => console.error(err));
    }
  }, []);

  return (
    <div>
      <Navbar />

      <div className="p-8">
        <h1 className="text-3xl font-bold mb-6">ContractorConnect</h1>

        {/* User View */}
        {userRole === "user" && (
          <>
            <h2 className="text-xl font-semibold mb-4">Top Contractors</h2>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              {contractors.length > 0 ? (
                contractors.map((c: Contractor) => (
                  <ContractorCard key={c._id} {...c} />
                ))
              ) : (
                <p>No contractors found.</p>
              )}
            </div>

            <h2 className="text-xl font-semibold mt-10 mb-4">Popular Services</h2>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              {services.length > 0 ? (
                services.map((s: Service) => <ServiceCard key={s._id} {...s} />)
              ) : (
                <p>No services found.</p>
              )}
            </div>
          </>
        )}

        {/* Contractor View */}
        {userRole === "contractor" && (
          <>
            <h2 className="text-xl font-semibold mb-4">Apartment Members Looking for Services</h2>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              {members.length > 0 ? (
                members.map((m: User) => (
                  <div key={m._id} className="border p-4 rounded shadow">
                    <h3 className="font-bold">{m.name}</h3>
                    <p>{m.apartmentInfo}</p>
                    <p>Contact: {m.contact || "Not provided"}</p>
                  </div>
                ))
              ) : (
                <p>No apartment members found.</p>
              )}
            </div>
          </>
        )}

        {/* Not logged in */}
        {!userRole && (
          <p className="text-center text-gray-500 mt-10">
            Please login to view content.
          </p>
        )}
      </div>
    </div>
  );
}

