import React from "react";

type Props = {
  name: string;
  speciality: string;
  rating: number;
  image?: string; // optional contractor image
};

export default function ContractorCard({ name, speciality, rating, image }: Props) {
  return (
    <div className="border rounded-lg shadow hover:shadow-lg transition p-4 flex flex-col items-center bg-white">
      <img
        src={image || "/default-contractor.jpg"} // default image if none
        alt={name}
        className="w-24 h-24 rounded-full mb-4 object-cover border"
      />
      <h3 className="text-lg font-bold">{name}</h3>
      <p className="text-gray-600">{speciality}</p>
      <div className="flex items-center mt-2">
        <span className="text-yellow-500 mr-2">
          {"★".repeat(Math.floor(rating))}{" "}
          {"☆".repeat(5 - Math.floor(rating))}
        </span>
        <span className="text-gray-500 text-sm">({rating.toFixed(1)})</span>
      </div>
      <button className="mt-4 bg-blue-500 text-white px-4 py-1 rounded hover:bg-blue-600 transition">
        Contact
      </button>
    </div>
  );
}
